import React from 'react'
import PropTypes from 'prop-types'

class NewTaskTags extends React.Component{
	constructor(){
		super()

		this.addTag = this.addTag.bind(this)

		this.state = {
			isOn:false,
			tags:['Urgent', 'Internal','Design','Add Functionality' ]
		}
	}

	addTag(e) {
		e.preventDefault()
		this.props.handleAddTag(e.target.value)
	}

	render(){
		return (
			<ul>
				{this.state.tags.map((tag,i) => {
					return (
						<li key={i}>
							<button name={tag} value={tag} onClick={this.addTag}>{tag} </button>
						</li>
					)
				})}
			</ul>
		)
	}

}

NewTaskTags.propTypes = {
	handleAddTag: PropTypes.func.isRequired,

}

export default NewTaskTags



