import React from 'react'
import PropTypes from 'prop-types'
import NewTaskTags from './NewTaskTags'
import { connect } from 'react-redux'

import { addTask, addTag } from '../actions'

class NewTask extends React.Component {

	constructor(props) {
		super(props)

		this.state = {
			tags:[],
		}

		this.handleChange = this.handleChange.bind(this)
		this.handleSubmit = this.handleSubmit.bind(this)
		this.addTag = this.addTag.bind(this)

	}

	handleSubmit(e) {
		e.preventDefault()

		//Dispatch action!!!!

		console.log(this)

		this.props.addTask({
			title: this.state.title,
			goal : this.state.goal,
			tags : this.state.tags,
		})

		this.setState({
			title:'',
			goal:'',
			tags:[],
		})
	}

	isOn(e){
		e.preventDefault()
		e.isTagOn = 'tagIsOn'
	}

	handleChange(event) {
		this.setState({
			[event.target.name]: event.target.value
		})
	}

	addTag(tagType) {
		let tags = this.state.tags
		tags.push(tagType)

		this.setState({
			tags: tags
		})
	}

	render() {
		// let isTagOn = ''

		return (
			<form onSubmit={this.handleSubmit}>
				<NewTaskTags handleAddTag={this.addTag} />
				<label>
					Task:
					<input placeholder="Task Title" name="title" type="text" onChange={this.handleChange} value={this.state.title}  />
				</label>
				<label>
					Task Objective:
					<input placeholder = "Task objective" type="text" name = "goal" onChange={this.handleChange} value={this.state.goal} />
				</label>
				<button id="submitBtn" type="submit" value="Submit" onChange={this.handleSubmit}>submit</button>
			</form>
		)
	}
}
// <button className ={isTagOn} name="Urgent" value="Urgent" onClick={this.addTag,this.isOn}>Urgent </button>
// <button name="Design" value="Design" onClick={this.addTag,this.isOn}>Design</button>
// <button name="Add Functionality" value="Add functionality" onClick={this.addTag,this.isOn}>Add Functionality</button>
// <button name="Internal" value="Internal" onClick={this.addTag,this.isOn}>Internal</button>

NewTask.propTypes = {
	addTask: PropTypes.func.isRequired,
}

export default connect(
	state => ({
		tasks: state.tasks,
		tags: state.tags.list
	}), {
		addTask,
		addTag
	}
)(NewTask)



